/* src/components/Dashboard.css */
/* Global resets */
html, body, #root {
  height: 100%;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

/* Dashboard container: fits entire viewport */
.dashboard-container {
  background-color: var(--bg-color); /* Ensure the container uses the theme background */
  display: grid;
  grid-template-rows: auto auto 1fr; /* header, top, bottom */
  gap: 10px;
  height: 100vh;
  padding: 10px;
  box-sizing: border-box;
  overflow: hidden;
}

/* Dashboard header for theme toggle */
.dashboard-header {
  text-align: center;
}

/* Dashboard top row - Vulnerabilities Counter */
.dashboard-top {
  /* Auto height */
}

/* Dashboard middle row - Pie Charts */
.dashboard-middle {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 10px;
  overflow: hidden;
}

/* Constrain pie chart containers */
.chart-container {
  max-height: 250px;  /* adjust as needed */
  overflow: hidden;
}

/* Dashboard bottom row - Over Time chart and Top Vulnerabilities */
.dashboard-bottom {
  display: grid;
  grid-template-columns: 2fr 1fr;
  gap: 10px;
  overflow: auto; /* internal scrolling if needed */
}

/* Constrain the top vulnerabilities list */
.list-container {
  max-height: 300px;
  overflow-y: auto;
}
